  src: url("../fonts/open-sans-regular.woff2") format("woff2"), url("../fonts/open-sans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/open-sans-300.woff2") format("woff2"), url("../fonts/open-sans-300.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap; }

body {
  position: relative;
  min-height: 100vh;
  max-width: 100vw;
  font-family: "Open Sans", "Arial", sans-serif;
  min-width: 768px;
  font-size: 14px;
  line-height: 1.3;
  font-weight: 400;
  color: #1a1a1a;
  overflow-x: hidden; }

.wrapper {
  margin: 0 auto;
  width: 100%;
  max-width: 1108px;
  box-sizing: border-box;
  padding: 0 20px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.intro {
  background-color: #8c5d79;
  padding: 60px 0;
  padding-bottom: 112px; }

.intro__list {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  min-height: 355px;
  z-index: 1;
  font-family: "Myriad Pro", "Arial", sans-serif;
  font-size: 22px;
  line-height: 24px; }

.intro__cols {
  float: left;
  text-align: right;
  width: calc(50% - 176px); }
  .intro__cols--right {
    float: right;
    text-align: left; }
    .intro__cols--right .intro__item {
      padding-left: 97px; }
      .intro__cols--right .intro__item::before {
        content: "";
        position: absolute;
        width: 85px;
        height: 85px;
        background-color: #ffffff;
        border-radius: 50%;
        left: 0; }
      .intro__cols--right .intro__item:nth-child(1) {
        margin-left: -90px; }
      .intro__cols--right .intro__item:nth-child(2) {
        margin-left: -60px;
        margin-top: 45px;
        margin-bottom: 45px; }
      .intro__cols--right .intro__item:nth-child(3) {
        margin-left: -90px; }
  .intro__cols--left .intro__item {
    padding-right: 97px; }
    .intro__cols--left .intro__item::before {
      content: "";
      position: absolute;
      width: 87px;
      height: 87px;
      background-color: #ffffff;
      border-radius: 50%;
      right: 0; }
    .intro__cols--left .intro__item:nth-child(1) {
      margin-right: -90px; }
    .intro__cols--left .intro__item:nth-child(2) {
      margin-right: -60px;
      margin-top: 45px;
      margin-bottom: 45px; }
    .intro__cols--left .intro__item:nth-child(3) {
      margin-right: -90px; }

.intro__item {
  position: relative;
  margin: 0;
  height: 87px;
  line-height: 87px; }

.intro__img-container {
  width: 280px;
  height: 110px;
  background-color: #5a9aa8; }

.intro__container {
  position: relative;
  margin-top: 117px; }

.intro__round-img-container {
  position: absolute;
  margin: 0;
  width: 348px;
  height: 348px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: #5a9aa8;
  border-radius: 50%; }

.intro__item-inner {
  display: inline-block;
  vertical-align: middle;
  padding-bottom: 10px;
  line-height: 1.25; }

.main-content {
  margin-top: 30px;
  padding-bottom: 30px;
  float: left;
  overflow: hidden;
  background-color: #ffffff; }

.main-content__container {
