      background-color: transparent;
      margin-right: -30px; } }

.main-content__list {
  position: relative;
  display: table;
  width: 100%;
  padding-bottom: 29px; }

.main-content__row {
  display: table-row;
  width: 100%;
  box-sizing: border-box; }

.main-content__left {
  position: relative;
  float: left;
  width: 71%;
  margin-bottom: 30px;
  right: 75%; }
  @media (max-width: 860px) {
    .main-content__left {
      width: 100%;
      right: 0;
      float: none; } }

.main-content__right {
  position: relative;
  float: right;
  width: 25%;
  background-color: #8c5d79;
  height: 100%;
  right: 75%; }
  @media (max-width: 860px) {
    .main-content__right {
      width: 100%;
      right: 0;
      min-height: 200px;
      float: none; } }

.main-content__header {
  float: left;
  overflow: hidden;
  background-color: #89c39b;
  width: calc(100% - 30px); }

.main-content__header-container {
  float: left;
  background-color: #8c5d79;
  position: relative;
  left: calc(100% - 138px); }

.main-content__item {
  display: table-cell;
  width: 50%;
  max-width: 368px;
  padding-right: 30px;
  padding-top: 45px;
  padding-bottom: 15px;
  box-sizing: border-box;
  cursor: pointer;
  height: 100%;
  line-height: 1.25; }
  .main-content__item h3 {
    margin: 0;
    margin-top: 25px;
    margin-bottom: 8px;
    text-align: center;
    font-weight: 400;
    font-size: 24px;
    text-transform: uppercase; }
  .main-content__item figure {
    display: block;
    width: 100%;
    height: 200px;
    margin: 0;
    background-color: #89c39b; }
  .main-content__item:hover figure {
    background-color: #5a9aa8; }
  .main-content__item--button {
    text-align: center;
    vertical-align: middle;
    margin: 0 auto;
    position: relative; }
    .main-content__item--button .btn {
      margin-top: -25px; }
    .main-content__item--button.width {
      min-height: 80px;
      padding-right: 0; }
      .main-content__item--button.width .btn {
        position: relative;
        top: 0;
        left: 50%;
        margin-top: 0; }

.main-content__header-links {
  float: left;
  width: 138px;
  text-align: center;
  padding: 17px 20px;
  padding-bottom: 7px;
  box-sizing: border-box;
  right: calc(100% - 138px);
  position: relative; }

.main-content__header-left {
  float: left;
  box-sizing: border-box;
  width: calc(100% - 138px);
  padding: 20px 13px;
  padding-left: 35px;
  font-family: "Myriad Pro", "Arial", sans-serif;
  font-size: 18px;
  position: relative;
  right: calc(100% - 138px); }

.main-content__header-img {
  margin-top: 12px;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #8c5d79;
  float: left; }

.main-content__header-content {
  float: right;
  width: calc(100% - 135px);
  line-height: 1.3; }
  .main-content__header-content h2 {
    margin: 0;
    margin-bottom: 7px;
    font-size: 19px;
    font-weight: 700; }
  .main-content__header-content p {
    margin: 0; }

.footer {
  clear: both;
  background-color: #f5f5f5; }

.footer__wrapper {
  padding: 20px 0;
  line-height: 300px;
  height: 300px;
  text-align: center;
