  display: inline-block;
  margin-bottom: 10px;
  color: #ffffff; }
  .header-links__container span {
    display: block;
    margin-top: 5px;
    padding-top: 12px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    box-sizing: border-box;
    border: 3px solid #ffffff;
    font-weight: 700; }

.btn {
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #8c5d79;
  border: none;
  padding: 0;
  margin: 0;
  padding: 13px;
  padding-bottom: 21px;
  width: 100%;
  max-width: 250px;
  box-sizing: border-box;
  border-radius: 5px;
  cursor: pointer;
  font-size: 30px; }
  .btn:hover {
    opacity: 0.7; }
